<template>
    <div class="sub_Wrapper">
      <Header></Header>
      <div class="sub_contents">
        <div class="inner">
            <Error></Error>
        </div>
      </div>
      <Footer></Footer>
      <ErrorpopUp v-if="isErrorPopUpVisible" @close="isErrorPopUpVisible = false"></ErrorpopUp>
    <!--   -->
    <BasicPopup
      v-if="isPopUpVisible"
      :title="popupTitle"
      :footerText="popupFooterText"
      :footerLink="popupFooterLink"
      :showCheckBox="true" 
      @close="isPopUpVisible = false"
    >
      <component :is="popupContent"></component>
    </BasicPopup>
    </div>
  </template>
  
  
  <script>
  import Header from '@/components/Header.vue'
  import Footer from '@/components/Footer.vue'
  import Error from '@/components/ui/error/Error.vue';
  import ErrorpopUp from '@/components/ui/popup/ErrorpopUp.vue'
  import BasicPopup from '@/layouts/popup/BasicpopUp.vue'
  import MainpopUp from '@/components/ui/popup/MainpopUp.vue'


  export default {
    name: 'ErrorLayout',
    components: {
      Header,
      Footer,
      Error,
      ErrorpopUp,
      BasicPopup,
      MainpopUp
    },

    
    data() {
    return {
      isPopUpVisible: true,           // BasicPopup  
      isErrorPopUpVisible: true,     // ErrorpopUp  
      popupTitle: '',    //   
      popupFooterText: '',        //    
      popupFooterLink: '/',           //    
      popupContent: 'MainpopUp',      //   
      
    };
  },
  }
  </script>
  