export default {
    name: 'OplibrarydetailPage',
    components : {
        BasicPopup,
        VulnerabilitypopUp,
        LicensepopUp
    },
    setup() {
        const showPopup = ref(false);
        const popupContent = ref(null);
        const popupTitle = ref('');


        const openVulnerabilityPopup = () => {
            popupContent.value = VulnerabilitypopUp;
            popupTitle.value = " ";
            showPopup.value = true;
        };

        const openLicensepopUp = () => {
            popupContent.value = LicensepopUp;
            popupTitle.value = " ";
            showPopup.value = true;
        };

        return {
            showPopup,
            popupContent,
            popupTitle,
            openVulnerabilityPopup,
            openLicensepopUp
        };
    }

}
