        { id: 4, name: 'JKL    ', version: '1.13.1', risk: '', cvss: '10', license: 'Apache  1', date: '2024-12-24', issue: 'N', disable: false },
        { id: 5, name: 'MNO    ', version: '1.13.1', risk: '', cvss: '10', license: 'Apache  1', date: '2024-12-24', issue: 'N', disable: false },
        { id: 6, name: 'PQR    ', version: '1.13.1', risk: '', cvss: '10', license: 'Apache  1', date: '2024-12-24', issue: 'N', disable: false },
