        { id: 2, name: '/ oss', classification: ' ', OSS: 'Cloud Foundry', version: '8.0', licenseNum: '3', vulnerability: '7', disable: false },
        { id: 3, name: '/ oss', classification: '   ', OSS: 'Cloud Foundry', version: '8.0', licenseNum: '3', vulnerability: '7', disable: false },
        { id: 4, name: ' oss', classification: '', OSS: 'Cloud Foundry', version: '8.0', licenseNum: '3', vulnerability: '7', disable: false },
