      showSelectionOptions: false,
      allItemsSelected: false,
      orderArrow: require('@/assets/images/sub/orderArrow.png'), 
      selectedCount: 0, 
    };
  },
  computed: {
    nonDisabledItems() {
      return this.items.filter(item => !item.disable);
    }
  },
  methods: {
    toggleSelectAll(event) {
      this.selectAll = event.target.checked;
      if (this.selectAll) {
        this.checkedItems = this.nonDisabledItems.map(item => item.id);
        this.allItemsSelected = true;
      } else {
        this.checkedItems = [];
        this.allItemsSelected = false;
      }
      this.showSelectionOptions = this.checkedItems.length > 0;
    },
    selectAllItems() {
      this.checkedItems = this.nonDisabledItems.map(item => item.id);
      this.allItemsSelected = true;
      this.showSelectionOptions = true;
    },
    deselectAllItems() {
      this.checkedItems = [];
      this.allItemsSelected = false;
      this.showSelectionOptions = false;
    },
    riskClass(risk) {
      switch (risk) {
        case '': return 'emergency';
        case '': return 'high';
        case '': return 'mid';
        case '': return 'low';
        default: return '';
      }
    }
  },
  watch: {
    checkedItems(newVal) {
      const selectableItemsCount = this.nonDisabledItems.length;
      this.selectAll = newVal.length === selectableItemsCount;
      this.showSelectionOptions = newVal.length > 0;
      this.allItemsSelected = this.selectAll;
      this.selectedCount = newVal.length;  
    }
  },
  
