<script>
import BasicPopup from '@/layouts/popup/BasicpopUp.vue'
import VulnerabilitypopUp from '@/components/ui/popup/VulnerabilitypopUp.vue'
import LicensepopUp from '@/components/ui/popup/LicensepopUp.vue'
import IssueHistory from '@/components/ui/popup/IssueHistory.vue' 
import { ref } from 'vue';

export default {
    name : "LibraryModification",
    components : {
        BasicPopup,
        VulnerabilitypopUp,
        LicensepopUp
    },
    setup() {
        const showPopup = ref(false);
        const popupContent = ref(null);
        const popupTitle = ref('');


        const openVulnerabilityPopup = () => {
            popupContent.value = VulnerabilitypopUp;
            popupTitle.value = " ";
            showPopup.value = true;
        };

        const openLicensepopUp = () => {
            popupContent.value = LicensepopUp;
            popupTitle.value = " ";
            showPopup.value = true;
        };

        const openIssueHistory = () => {
            popupContent.value = IssueHistory;
            popupTitle.value = "    ";
            showPopup.value = true;
        };

        return {
            showPopup,
            popupContent,
            popupTitle,
            openVulnerabilityPopup,
            openLicensepopUp,
            openIssueHistory
        };
    }
}
