<template>
    <div class="situationSection">
        <div class="situationContent">
            <div class="situationW">
                <TabMenu :tabs="tabs">
                    <template v-slot:tab1>
                        <div class="tabWrap">
                            <ul>
                                <li v-for="item in appliItem" :key="item.id">
                                    <div>
                                        <span class="systemScript">
                                            <a href="#"><span class="systemName">[{{item.systemName}}]</span> 
                                                <span class="workName">[{{item.workName}}]</span>{{item.content}}
                                            </a>
                                        </span>
                                        <span class="date">{{item.date}}</span>
                                    </div>
                                    <span :class="getClass(item.payIndication)">{{item.payIndication}}</span>
                                </li>
                            </ul>
                            <router-link to="/" class="goArrow"><span></span></router-link>
                        </div>
                    </template>

                    <template v-slot:tab2>
                        <div class="tabWrap">
                            <ul>
                                <li v-for="item in planiItem" :key="item.id">
                                    <div>
                                        <span class="systemScript">
                                            <a href="#"><span class="systemName">[{{item.systemName}}]</span> 
                                                <span class="workName">[{{item.workName}}]</span>{{item.content}}
                                            </a>
                                        </span>
                                        <span class="date">{{item.date}}</span>
                                    </div>
                                    <span :class="getClass(item.payIndication)">{{item.payIndication}}</span>
                                </li>
                            </ul>
                            <router-link to="/" class="goArrow"><span></span></router-link>
                        </div>
                    </template>
                </TabMenu>
            </div>

            <div class="mySystemW">
                <div class="flex-justify-space">
                    <h2 class="h2tit">My </h2>
                    <router-link to="/" class="goArrow"><span></span></router-link>
                </div>
                <div class="systemW">
                    <ul>
                        <li v-for="item in mySystemItem" :key="item.id">   
                            <a href="">{{item.content}}</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import TabMenu from '../ui/tab/TabMenu.vue'

export default {
    name: 'SituationSection',
    components: {
        TabMenu
    }, 
    data() {
        return {
            tabs: [
                { name: 'tab1', label: '/ ' }, 
                { name: 'tab2', label: '  ' },
            ], 
            appliItem: [
                {id: 1, systemName: ' A', workName: 'OSS', content: "[A] [  OSS] apache tomcat apache tomcat apache tomcat", date: '2024-12-10', payIndication: '1 '},
                {id: 2, systemName: ' B', workName: 'OSS', content: "[A] [  OSS] apache tomcat apache tomcat apache tomcat", date: '2024-12-10', payIndication: ' 2 '}  
            ], 
            planiItem: [
                {id: 1, systemName: ' C', workName: 'OSS', content: "[A] [  OSS] apache tomcat apache tomcat apache tomcat", date: '2024-12-10', payIndication: '1 '},
                {id: 2, systemName: ' D', workName: 'OSS', content: "[A] [  OSS] apache tomcat apache tomcat apache tomcat", date: '2024-12-10', payIndication: ' 2 '} 
            ],
            mySystemItem: [
                {id: 1, content: 'mySystemItem  test text .111' },
                {id: 2, content: 'mySystemItem  test text .222' },
                {id: 3, content: 'mySystemItem  test text .333' },
            ],
        }
    }, 
    methods: {
        getClass(text) {
            const baseClass = 'payIndication'; 

            switch (text) {
                case '1 ': 
                    return [baseClass, 'style1']; 
                case ' 2 ': 
                    return [baseClass, 'style2']; 
                default : 
                return [baseClass]; 
            }
        }
    }
}
</script>