<template>
  <div class="CommentCon">
    <ul class="comentList">
      <li v-for="(comment, index) in comments" :key="index" :class="{ user: comment.identity === '' }">
        <div class="identityBox">
          <span class="identity">{{ comment.identity || '' }}</span>
        </div>
        <div class="comentW">
          <ul>
            <li class="team">{{ comment.team }}</li>
            
            <li class="name">{{ comment.name }}</li>
            <li class="creationTime">{{ comment.creationTime }}</li>
          </ul>
          <p>
            {{ comment.text }}
            <span class="comentClose" v-if="comment.identity === ''" @click="deleteComment(index)"></span>
          </p>
        </div>
      </li>
    </ul>
    <div class="comentBox">
      <div class="comentTop">
        <ul class="comenttitList">
          <li class="team">IT</li>
          <li class="name"></li>
          <li class="hour">2024-12-30 09:16</li>
        </ul>
      </div>
      <div class="comentForm">
        <form @submit.prevent="submitComment">
          <div class="comentBox">
            <textarea
              v-model="newComment"
              placeholder="   200  ."
              class="comenttxarea"
            ></textarea>
            <div class="writeNum">
              <span class="current">{{ newComment.length }}</span> / <span class="total">200</span>
            </div>
          </div>
          <button type="submit" class="comentBtn"></button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommentSection',
  props: {
    comments: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      newComment: ''
    };
  },
  methods: {
    submitComment() {
      if (this.newComment.trim().length === 0 || this.newComment.length > 200) {
        return; //  
      }
      const commentData = {
        identity: '', //   identity,   
        team: 'IT',
        name: '', //    
        creationTime: new Date().toLocaleString(),
        text: this.newComment
      };
      this.$emit('add-comment', commentData);
      this.newComment = ''; //     
    },
    deleteComment(index) {
      this.$emit('delete-comment', index);
    }
  }
};
</script>
