            popupTitle.value = "";
            showPopup.value = true;
            popupFooterText.value = "";
            popupFooterLink.value = "/";
        };

        const openOsspopUp = () => {
            popupContent.value = OsspopUp;
            popupTitle.value = "OSS ";
            showPopup.value = true;
            popupFooterText.value = "";
            popupFooterLink.value = "/";
        };

        const openWeaknesspopUp = () => {
            popupContent.value = WeaknesspopUp;
            popupTitle.value = "  ";
            showPopup.value = true;
            popupFooterText.value = "";
            popupFooterLink.value = "/";
        };


        return {
            showPopup,
            popupContent,
            popupTitle,
            popupFooterText,
            popupFooterLink,
            openChecklistpopUp,
            openOsspopUp,
            openWeaknesspopUp
        };
  },
}
